<template>
  <view class="page">

    <!-- Êé®ËçêÊ®°ÂºèÂÖ®Â±èÂç°Áâá -->
    <view v-if="recommendMode" class="recom-mask">
      <view class="recom-card">
        <image class="recom-cover" :src="(reco && reco.cover) || ''" mode="aspectFill" />
        <view class="recom-info">
          <text class="recom-name">{{ (reco && reco.name) || '‰ªäÊó•Êé®Ëçê' }}</text>
          <view class="recom-meta">
            <view class="tag" :class="((reco && reco.level) === 'ÁÆÄÂçï') ? 'tag-green' : 'tag-orange'">{{ (reco && reco.level) || 'ÈÄÇ‰∏≠' }}</view>
            <text class="time">{{ (reco && reco.time) || '--' }}min</text>
          </view>
        </view>
        <view class="recom-actions">
          <button class="btn cancel" @click="cancelRecommend">ÂèñÊ∂à</button>
          <button class="btn change" @click="nextRecommend">Êç¢‰∏Ä‰∏™</button>
        </view>
      </view>
    </view>
    <!-- È°∂ÈÉ®Ê†è -->
    <view class="header">
      <text class="title">ËèúË∞±Â§ßÂÖ®</text>
      <view class="search-btn" @click="onSearch">
        <text class="icon">üîç</text>
        <text class="label">ÊêúÁ¥¢</text>
      </view>
    </view>


    <!-- ÂàÜÁ±ªÁ≠õÈÄâÔºàÈ°∂ÊéíÂ±ïÁ§∫ 4 ‰∏™ + ÁúÅÁï•Âè∑Ôºâ -->
    <scroll-view class="chips" scroll-x>
      <view
        v-for="c in topCats"
        :key="c.key"
        :class="['chip', activeCat === c.key ? 'active' : '']"
        @click="activeCat = c.key"
      >
        {{ c.name }}
      </view>
      <view
        v-if="moreCats && moreCats.length"
        class="chip more"
        @click="toggleCats"
      >¬∑¬∑¬∑</view>
    </scroll-view>

    <!-- Â±ïÂºÄÂêéÂú®‰∏ãÊñπÂ±ïÁ§∫Êõ¥Â§öÊ†áÁ≠æ -->
    <view v-if="showAllCats && moreCats && moreCats.length" class="chips-more">
      <view
        v-for="c in moreCats"
        :key="c.key"
        :class="['chip', activeCat === c.key ? 'active' : '']"
        @click="activeCat = c.key"
      >
        {{ c.name }}
      </view>
    </view>

    <!-- ‰∏§ÂàóÂç°ÁâáÁΩëÊ†º -->
    <view class="grid">
      <view class="card" v-for="r in filteredList" :key="r.id" @click="openRecipe(r)">
        <image class="cover" :src="r.cover" mode="aspectFill" />
        <view class="info">
          <text class="name">{{ r.name }}</text>
          <view class="meta">
            <view class="tag" :class="r.level === 'ÁÆÄÂçï' ? 'tag-green' : 'tag-orange'">{{ r.level }}</view>
          </view>
        </view>
      </view>
    </view>

    <view style="height: 24rpx;" />
  </view>
</template>

<script>
import {getRecipes, mapTypeIdToCat, DEFAULT_COVER} from '../../api/recipes.js' 
export default {
  onLoad(options) {
    if (options && options.cat) {
      this.activeCat = options.cat;
    }
    // Êé®ËçêÊ®°ÂºèÂÖ•Âè£Ôºö/pages/recipes/index?recommend=1
    if (options && (options.recommend === '1' || options.recommend === 1 || options.recommend === true)) {
      this.recommendMode = true;
      this.pickRandom();
    }
    // ÈöèÊú∫ËèúË∞±ÂÖ•Âè£Ôºö‰ªéÊú¨È°µÊåëÈÄâ4‰∏™Âπ∂Á´ãÂàªÊâìÂºÄÁ¨¨‰∏Ä‰∏™ËØ¶ÊÉÖ
    if (options && (options.random === '1' || options.random === 1 || options.random === true)) {
      const picked = this.pickFour();
      try {
        uni.setStorageSync('random_selection', (picked || []).map(x => x.id));
        uni.setStorageSync('random_selection_data', picked || []);
      } catch(e) {}
      if (picked && picked.length) {
        this.openRecipe(picked[0]);
      } else {
        uni.showToast({ title: 'ÊöÇÊó†ËèúË∞±ÂèØÈÄâ', icon: 'none' });
      }
    }
  },
  onShow() {
    const cat = uni.getStorageSync('recipes_cat');
    if (cat) {
      this.activeCat = cat;
      uni.removeStorageSync('recipes_cat');
    }
    // Ë∞ÉÁî® /api/recipes/list Êé•Âè£
    console.log('ÂºÄÂßãËØ∑Ê±ÇËèúË∞±Êï∞ÊçÆ...')
    getRecipes()
      .then(res => {
        console.log('ËèúË∞±Êé•Âè£ÂìçÂ∫îÊï∞ÊçÆ:', res)
        const arr = Array.isArray(res) ? res : []
        this.list = arr.map(r => ({
          id: r.id,                 // Recipe.id
          name: r.name || 'ËèúË∞±',   // Recipe.name
          cat: mapTypeIdToCat(r.typeId),   // Â∞ÜÂêéÁ´ØtypeIdÊò†Â∞ÑÂà∞ÂâçÁ´ØÂàÜÁ±ªkey
          cover: r.feature || DEFAULT_COVER,     // ‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑfeatureÂ≠óÊÆµÊàñÈªòËÆ§Â∞ÅÈù¢
          // ‰øùÁïôÂêéÁ´ØÂéüÂßãÊï∞ÊçÆÔºåÊñπ‰æøÂêéÁª≠‰ΩøÁî®
          originalData: r
        }))
        console.log('ÊàêÂäüÂä†ËΩΩËèúË∞±Êï∞Èáè:', this.list.length)
      })
      .catch(err => {
        console.error('Ëé∑ÂèñËèúË∞±ÂàóË°®Â§±Ë¥•Ôºö', err)
        console.log('ÈîôËØØËØ¶ÊÉÖ:', JSON.stringify(err))
        // ‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ‰Ωú‰∏∫ÂêéÂ§áÊñπÊ°à
        this.list = this.getMockData()
        uni.showToast({
          title: 'ÂêéÁ´ØÊúçÂä°‰∏çÂèØÁî®Ôºå‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ',
          icon: 'none',
          duration: 2000
        })
      })
  },
  data() {
    return {
      recommendMode: false,
      reco: null,
      activeCat: 'all',
      cats: [
        { key: 'all', name: 'ÂÖ®ÈÉ®' },
        { key: 'cn', name: '‰∏≠Âºè' },
        { key: 'west', name: 'Ë•øÂºè' },
        { key: 'jp', name: 'Êó•Âºè' },
        { key: 'kr', name: 'Èü©Âºè' },
        { key: 'chuancai', name: 'Â∑ùËèú' },
        { key: 'yuecai', name: 'Á≤§Ëèú' },
        { key: 'xianggai', name: 'ÊπòËèú' },
        { key: 'zhecai', name: 'ÊµôËèú' },
        { key: 'dongbei', name: '‰∏úÂåó' },
        { key: 'xibei', name: 'Ë•øÂåó' },
        { key: 'jiachang', name: 'ÂÆ∂Â∏∏' },
        { key: 'sushi', name: 'Á¥†È£ü' },
        { key: 'shaokao', name: 'ÁÉßÁÉ§' },
        { key: 'tianpin', name: 'ÁîúÂìÅ' }
      ],
      showAllCats: false,
      maxCats: 8,
      topCount: 6,
      // Êé•Âè£ËøîÂõûÊï∞ÊçÆÂàóË°®
      list: []
    }
  },
  computed: {
    filteredList() {
      if (this.activeCat === 'all') return this.list
      return this.list.filter(i => i.cat === this.activeCat)
    },
    // Ê†πÊçÆÂΩìÂâçÂàóË°®ÂìÅÁ±ªÂá∫Áé∞È¢ëÊ¨°ÔºåÁÉ≠Èó®Âú®ÂâçÔºà‰øùÊåÅ 'all' ÁΩÆÈ°∂Ôºâ
    orderedCats() {
      const arr = this.cats || []
      const counts = {}
      ;(this.list || []).forEach(i => {
        counts[i.cat] = (counts[i.cat] || 0) + 1
      })
      const head = arr.filter(c => c.key === 'all')
      const rest = arr.filter(c => c.key !== 'all')
        .sort((a, b) => (counts[b.key] || 0) - (counts[a.key] || 0))
      return [...head, ...rest]
    },
    topCats() {
      const arr = this.orderedCats || this.cats || []
      const n = Math.min(this.topCount || 8, arr.length)
      return arr.slice(0, n)
    },
    moreCats() {
      const arr = this.orderedCats || this.cats || []
      return arr.slice(4)
    }
  },
  methods: {
    // ÈöèÊú∫ÊåëÈÄâÂõõ‰∏™ËèúË∞±
    pickFour() {
      const src = this.list || [];
      const n = Math.min(4, src.length);
      const pool = [...src];
      const res = [];
      for (let i = 0; i < n; i++) {
        const idx = Math.floor(Math.random() * pool.length);
        res.push(pool.splice(idx, 1)[0]);
      }
      return res;
    },
    // Êé®ËçêÊ®°ÂºèÊñπÊ≥ï
    pickRandom() {
      if (!this.list || this.list.length === 0) {
        this.reco = null
        return
      }
      const idx = Math.floor(Math.random() * this.list.length)
      this.reco = this.list[idx]
    },
    cancelRecommend() {
      const pages = getCurrentPages && getCurrentPages()
      if (pages && pages.length > 1) {
        uni.navigateBack({ delta: 1 })
      } else {
        this.recommendMode = false
      }
    },
    nextRecommend() {
      this.pickRandom()
    },
    onSearch() {
      uni.showToast({ title: 'ÊêúÁ¥¢ÊöÇÊú™Êé•ÂÖ•', icon: 'none' })
    },
    openRecipe(r) {
      // Ë∑≥ËΩ¨Âà∞ËØ¶ÊÉÖÈ°µÂπ∂‰º†ÂèÇÔºàÂêçÁß∞/ÂÅöÊ≥ï/ÂéüÊñô/Ë∞ÉÊñô/ÁâπÊÄß/ÂõæÁâáÔºâ
      const q = [
        'id=' + encodeURIComponent(r.id || ''),
        'name=' + encodeURIComponent(r.name || ''),
        'imageUrl=' + encodeURIComponent(r.cover || '')
      ].join('&')
      uni.navigateTo({ url: '/pages/recipes/detail?' + q })
    },
    toggleCats() {
      this.showAllCats = !this.showAllCats
    }
  }
}
</script>

<style>
.page {
  padding: 24rpx;
  background: #f7f2e7;
  position: relative;
  min-height: 100vh;
  box-sizing: border-box;
}

/* È°∂ÈÉ®Ê†è */
.header {
  height: 88rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  font-size: 36rpx;
  font-weight: 700;
  color: #1a1f36;
}
.icon-search {
  font-size: 36rpx;
  color: #5f6368;
}
/* ÊòæÁúºÁöÑÊêúÁ¥¢ÊåâÈíÆ */
.search-btn {
  display: inline-flex;
  align-items: center;
  gap: 10rpx;
  padding: 10rpx 18rpx;
  background: linear-gradient(90deg, #FFE27A 0%, #FFC107 100%);
  color: #fff;
  border-radius: 999rpx;
  box-shadow: 0 8rpx 20rpx rgba(255,122,0,0.28);
}
.search-btn .icon {
  font-size: 30rpx;
}
.search-btn .label {
  font-size: 28rpx;
  font-weight: 600;
}

.subheading {
  margin-top: 8rpx;
  font-size: 26rpx;
  color: #6b7280;
}
/* ÂàÜÁ±ª Chip */
.chips {
  margin-top: 16rpx;
  white-space: nowrap;
}
.chip {
  display: inline-flex;
  padding: 12rpx 24rpx;
  background: #f3f4f6;
  color: #374151;
  border-radius: 999rpx;
  font-size: 26rpx;
  margin-right: 16rpx;
}
.chip.more {
  background: #e5e7eb;
  color: #374151;
}
/* ‰∏ãÊñπÊõ¥Â§öÊ†áÁ≠æÂÆπÂô®ÔºöÂÖÅËÆ∏Êç¢Ë°å */
.chips-more {
  margin-top: 12rpx;
  display: flex;
  flex-wrap: wrap;
}
.chip.active {
  color: #fff;
  background: linear-gradient(90deg, #FFE27A 0%, #FFC107 100%);
  box-shadow: 0 6rpx 16rpx rgba(255, 193, 7, 0.3);
}

/* ÁΩëÊ†º */
.grid {
  margin-top: 20rpx;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* Âç°Áâá */
.card {
  width: calc(50% - 10rpx);
  background: #fff;
  border-radius: 24rpx;
  overflow: hidden;
  margin-bottom: 20rpx;
  box-shadow: 0 10rpx 30rpx rgba(0,0,0,0.06);
}
.cover {
  width: 100%;
  height: 300rpx;
  background: #eee;
}
.info {
  padding: 18rpx;
}
.name {
  font-size: 28rpx;
  color: #1f2937;
}
.meta {
  margin-top: 14rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tag {
  padding: 6rpx 16rpx;
  border-radius: 999rpx;
  font-size: 22rpx;
}
.tag-green {
  background: #e9f8ee;
  color: #27ae60;
}
.tag-orange {
  background: #fff3e6;
  color: #FFC107;
}
.time {
  font-size: 24rpx;
  color: #6b7280;
}
/* Êé®ËçêÊ®°ÂºèÊ†∑Âºè */
.recom-mask {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.45);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.recom-card {
  width: 86%;
  background: #fff;
  border-radius: 24rpx;
  overflow: hidden;
  box-shadow: 0 22rpx 60rpx rgba(0,0,0,0.22);
}
.recom-cover {
  width: 100%;
  height: 360rpx;
  background: #eee;
}
.recom-info {
  padding: 24rpx;
}
.recom-name {
  font-size: 34rpx;
  font-weight: 800;
  color: #111827;
}
.recom-meta {
  margin-top: 16rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.recom-actions {
  display: flex;
  padding: 18rpx 22rpx 26rpx;
  gap: 18rpx;
}
.btn {
  flex: 1 1 0;
  height: 84rpx;
  line-height: 84rpx;
  text-align: center;
  border-radius: 16rpx;
  font-size: 28rpx;
}
.btn.cancel {
  background: #f3f4f6;
  color: #374151;
}
.btn.change {
  background: linear-gradient(90deg, #FFE27A 0%, #FFC107 100%);
  color: #fff;
  box-shadow: 0 8rpx 20rpx rgba(255,193,7,0.35);
}

/* Âè≥‰∏äËßíÂç°ÈÄöÂõæÊ†∑ÂºèÔºàÁªü‰∏ÄÔºâ */

</style>